.AdminWrite {
  text-align: left;
  position: relative;
  width: 100%;
  max-width: 1600px;
  margin: 0 auto;
}
.pageTitle {
  margin-top: 2rem;
  font-size: 40px;
}
.AdminWrite > form {
  width: 100%;
  margin: 2rem auto;
  border: 1px solid rgba(0, 0, 0, 0.303);
  padding: 50px;
  display: flex;
  flex-direction: column;
}

.AdminWrite input {
  display: block;
  margin: 0.5rem 0;
}

.AdminWrite > form > button {
  margin-top: 3rem;
}
.summary,
.mainInfo {
  width: 100%;
  display: flex;
  margin: 40px 0;
}
.summary > article,
.mainInfo > article {
  flex: 1;
  display: flex;
  flex-direction: column;
}

.summary > article:first-child,
.mainInfo > article:first-child {
  margin-right: 10px;
}

.summary > article:last-child,
.mainInfo > article:last-child {
  margin-left: 10px;
}

.summaryTitle {
  font-size: 40px;
  font-weight: 500;
}
